* {margin: 0;padding: 0;font-family: 'Noto Sans KR', sans-serif;}

ul,li {list-style: none; }

a {color: black; text-decoration: none; cursor: pointer;
}

button{
  cursor: pointer;
}

/* point : rgb(0,32,83)  #002053*/
/* sub : rgb(238,238,238)  #eee*/

$pointcolor :#002053;
$subcolor : #eee;


/* ------------------ 기본설정 ------------------ */


body{
  height: 4100px;
  position: relative;
}


.header_outer {
  width: 100%;
  z-index: 9999;
  .header {
    width: 1660px;
    height: 100px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    
    .logo {
      width: 120px;
      padding: 30px 0;
      box-sizing: border-box;

      img {
        width: 100%;
        height: auto;
        display: block;
      }
    }


    .gnb {
      line-height: 100px;

      ul {
        display: flex;
        justify-content: space-between;
        align-items: center;

        li {
          font-size: 18px;
          font-weight: bold;
        }

        .bar {
          background-color: #ccc;
          height: 12px;
          width: 1px;
          margin: 0 10px;
        }


        a {
          color: black;
          padding: 15px;

        }



      }
    }


    .icons {
      line-height: 100px;

      ul {
        display: flex;
        justify-content: space-between;

        li {
          width: 55px;
        }
      }
    }
  }


  .lnb_menu {
    width: 100%;
    position: absolute;
    z-index: 9999;
    top: 0;
    background-color: #fff;
    visibility: hidden;
    .lnb_header {
      width: 1660px;
      margin: 0 auto;
      display: flex;
      justify-content: space-between;

      .logo {
        width: 120px;
        padding: 30px 0;
        box-sizing: border-box;
        img {
          width: 100%;
          height: auto;
          display: block;
        }
      }


      .gnb {
        line-height: 100px;

        ul {
          display: flex;
          justify-content: space-between;
          align-items: center;

          li {
            font-size: 18px;
            font-weight: bold;
          }

          .bar {
            background-color: #ccc;
            height: 12px;
            width: 1px;
            margin: 0 10px;
          }


          a {
            color: black;
            padding: 15px;

          }



        }
      }


      .icons {
        line-height: 100px;

        ul {
          display: flex;
          justify-content: space-between;

          li {
            width: 55px;
          }
        }
      }
    }

    .lnb_list {
      width: 1300px;
      margin: 0 auto;
    }

    .lnb {
      display: flex;
      justify-content: space-between;
    }



  }
}


.footer_outer{
  width: 100%;
  margin: 100px auto 0;
  background-color: $subcolor;
  position: absolute;
  bottom: 0;


  .footer{
    width: 1660px;
    margin: 0 auto;
    padding : 60px 0 20px;


/* content01------------------------------------------------ */
    .footer_content_01{
      display: flex;
      justify-content: space-between;

      .footer_menu{
        width: 70%;
        display: flex;
        justify-content: space-around;
        .menu_list{
          text-align: center;
          h5{
            font-size: 20px;
          }li{
            padding: 10px 0;
          }

          
        } 
      }

      .footer_sns{
        width: 15%;
        .sns_list{
          display: flex;
          justify-content: space-between;
          font-size: 22px;
        }
        .search_box{
          margin-top: 20px;
          height: 45px;
          position: relative;
          .search{
            float: left;
            background-color: transparent;
            width: 100%;
            padding-left: 10px;
            height: 45px;
            border: 2px solid #000;
            box-sizing: border-box;
          }
          .search_btn{
            float: left;
            width: 25%;
            height: 45px;
            background-color: black;
            color: white;
            font-weight: bold;
            font-size: 15px;
            letter-spacing: 1px;
            border: 2px solid #000;
            cursor: pointer;
            position: absolute;
            right: 0;
          }
        }
        .cs_box{
          margin-top:20px;
          display: flex;
          justify-content: space-between;
          .nation{
            font-size: 12px;
            line-height: 2;
          }
          select{
            border: none;
            border-bottom: 1px solid #000;
            background-color: transparent;
            option{
              background-color: $subcolor;
              font-size: 12px;
            }
          }
        }
      }
    }



/* content02------------------------------------------------ */
    .footer_content_02{
      height: 260px;
      margin-top: 50px;


      
      .company_info{
        float: left;
        width: 85%;
        color: #777;
        font-size: 15px;
        line-height: 2;
        .info{
          span{
            margin-left: 20px;
            border-bottom: 1px solid #777;
            cursor: pointer;
            font-weight: bold;
            color: #000;
          }

        }
        .list{
          margin-top: 58px;
          display: flex;
          li{
            width: 10%;
            color: black;
            font-weight: bold;
            border-right: 1px solid #ccc;
            text-align: center;
            &:first-child{
              text-align: left;
              width: 7%;
            }
            &:last-child{
              border-right: none;
            }
          }
        }
        



      }
      
      
      .cs_info{
        float: left;
        .cs_btn{
          background-color:transparent;
          width: 100%;
          border-radius: 20px;
          height: 45px;
          border: 1px solid #555;
          font-size: 15px;
        }
        .cs_tel{
          font-weight: bold;
          font-size: 30px;
          margin-top: 20px;
        }.cs_mail{
          font-weight: bold;
          font-size: 25px;
        }
        .cs_time{
          font-size: 15px;
          line-height: 2;
        }
        .cs_copy{
          margin-top: 8px;
          color: #777;
          font-size: 13px;
          line-height: 2;
        }



      }
    }


/* line------------------------------------------------ */
    .line{
      border-bottom: 1px solid #ccc;
      margin: 40px 0;
    }

  }
}


/* ------------------ goods ------------------ */

.goods_box_outer{
  width: 1420px;
  margin: 50px auto 0px;

  .goods_box{
    display: flex;
    justify-content: space-between;
    .goods_img{
      display: flex;
      flex-wrap: wrap;
      width: 1100px;
      margin-right: 20px;
      video{
        width: 49%;
        height: auto;
        display: block;
        margin-right: 2%;
      }
      img{
        width: 49%;
        height: auto;
        display: block;
      }
      .banner{
        width: 100%;
        height: 200px;
        display: block;
        margin-top: 20px;
      }
    }
  }
  .goods_info{
    width: 300px;
    .infobox{
      margin-bottom: 30px;
    }

    .title_box{
      margin-bottom: 20px;
      .tag{
        display: flex;
        margin-bottom: 5px;
        li{
          font-size: 14px;
          padding-right: 10px;
          &::before{
            content: "";
            display: inline-block;
            width: 1px;
            height: 12px;
            background-color: #ccc;
            margin-right: 10px;
          }
          &:first-child::before{
            width: 0;
            margin-right: 0;
          }
        }
      }
    }

     .content_box{
      font-size: 13px;
      text-align: justify;
      color: #333;
      position: relative;
      .content_btn{
        background-color: #fff;
        border: none;
        font-weight: 400;
        font-size: 11px;
        color: rgb(153,153,153);
        text-decoration: underline;        position: absolute;
        left: 0;
      }
    } 


    .color_box{
      h4{
        margin-bottom: 10px;
      }
      ul{
        display: flex;
        li{
          margin-right: 10px;
          width: 70px;
          cursor: pointer;
          img{
            width: 100%;
            height: auto;
            display: block;
          }
        }
      }
    }

    .size_box{
      .size_title{
        display: flex;
        justify-content: space-between;
        margin-bottom: 10px;
        .stock{
          font-size: 13px;
          cursor: pointer;
          border: 1px solid #000;
          transition: all 0.3s ease-in;
          padding: 0 12px;
          &:hover{
            background-color: #000;
            color: white;
          }
        }
      }
      .size_type{
        display: flex;
        justify-content: space-between;
        li{
          input{
            display: none;
            &:checked ~ label{
              border: 1px solid #000;
            }
          }
          label{
            flex: 1;
            padding: 0 5px; 
            display: block;
            border: 1px solid rgb(238, 238, 238);
            font-size: 14px;
            width: 100%;
            height: 30px;
            line-height: 30px;
            border-radius: 5px;
            box-sizing: border-box;
            cursor: pointer;
            font-weight: 400;
            font-size: 11px;
          }
        }

      }
    }

    .quantity_box{
      h4{
        margin-bottom: 10px;
      }
      ul{
        display: flex;
        justify-content: space-between;
        align-items: center;
        border: 1px solid rgb(238,238,238);
        box-sizing: border-box;
        height: 40px;
        padding: 0 15px;
        li{
          cursor: pointer;
        }
      }
    }

    .price_box{
      ul{
        display: flex;
        justify-content: space-between;
      }
    }

    .buy_box{
        ul{
          display: flex;
          li{
            border: 1px solid rgb(238, 238, 238);
            border-radius: 50px;
            width: 50px;height: 40px;
            line-height: 40px;
            text-align: center;
            width: 25%;
            margin-right: 2%;
            cursor: pointer;
          }
          .buy{
            width: 50%;
            margin-right: 0%;
            font-weight: bold;
            transition: all 0.3s;

            &:hover{
              background-color: $pointcolor;
              border: 1px solid $pointcolor;
              color: #fff;
            }

          }
        }
    }

  }



}

/* ------------------ info ------------------ */


.info_tab{
  width: 1420px;
  margin: 100px auto;
  height: 1400px;
  .tab_menu{
    display: flex;
    text-align: center;
    box-sizing: border-box;
    .tab{
      border: none;
      background-color: #fff;
      width: 33%;
      height: 50px;
      line-height: 50px;
      border: 1px solid #ccc;
      border-right: none;
      font-weight: bold;
      font-size: 15px;
      border-bottom: 2.5px solid $pointcolor;
      box-sizing: border-box;
      &:last-child{
        border-right: 1px solid #ccc;
      }
    }
    .active01{
      border-right: 2.5px solid $pointcolor;
      border: 2px solid $pointcolor;
      border-bottom: none;
      box-sizing: border-box;
      &:last-child{
        border-right:  2.5px solid $pointcolor
      }
    }
  }

  .tab_content{

    .content{
      display: none;
      width: 900px;
      height: 500px;
      margin: 0 auto;
      padding-top: 100px;

    }
    .active01{
      display: block;
    }

    .content01{
      .fit_guide{
        margin-bottom: 50px;
        h3{
          text-align: center;
          margin-bottom: 20px;
        }
        ul{
          display: flex;
          justify-content: space-between;
          li{
            .tit{
              text-align: center;
              font-size: 14px;
              color: rgb(204,204,204);
              margin-top: 10px;
            }
            .txt{
              text-align: center;
              font-size: 13px;
              color: rgb(204,204,204);
              margin-top: 5px;
            }
          }

          .choose{
            img{
              border: 1px solid #000;
            }
            p{
              color: #000;
              font-weight: bold;
            }
          }

        }
      }
      
      .info_guide{
        margin-bottom: 50px;
        h3{
          text-align: center;
          margin-bottom: 20px;
        }
        .graph_box{
          .tit{
            font-size: 13px;
            font-weight: bold;
            margin-bottom: 6px;
          }
          .graph01{
            display: flex;
            justify-content: space-between;
            position: relative;
            margin-bottom: 30px;
            &::before{
              content: "";
              position: absolute;
              top: 0;
              left: 0;
              background-color: rgb(238,238,238);
              border-radius: 10px;
              width: 100%;
              height: 10px;
            }
            &>div{
              font-size: 13px;
              margin-top: 15px;
            }
            .check{
              &::after{
                content: "";
                display: block;
                background-color:rgb(170, 170, 170);
                width: 10px;
                height: 10px;
                position: absolute;
                top: 0;
                border-radius: 10px;
                
              }
            }

            .mid{
              font-weight: bold;
              &::after{
                left: 50%;
                transform: translateX(-50%);
                background-color: #2b2a2a;
              }
            }

            .last{
              &::after{
                right: 0;
              }
            }
          }

          .graph02{
            display: flex;
            justify-content: space-between;
            position: relative;
            &::before{
              content: "";
              position: absolute;
              top: 0;
              left: 0;
              background-color: rgb(238,238,238);
              border-radius: 10px;
              width: 100%;
              height: 10px;
            }
            &>div{
              font-size: 13px;
              margin-top: 15px;
            }
            .check{
              &::after{
                content: "";
                display: block;
                background-color:rgb(170, 170, 170);
                width: 10px;
                height: 10px;
                position: absolute;
                top: 0;
                border-radius: 10px;
                
              }
            }

            .mid{
              &::after{
                left: 50%;
                transform: translateX(-50%);

              }
            }

            .last{
              font-weight: bold;
              &::after{
                right: 0;
                background-color: #2b2a2a;
              }
            }
          }
        }

      }

      .size_guide{
        h3{
          text-align: center;
          margin-bottom: 20px;
        }
        .cm{
          text-align: right;
          font-size: 13px;
          margin-bottom: 10px;
          color: #555;
        }
        dl{
          display: flex;
          font-size: 13px;
          border-top: 1px solid #e4e4e4;
          height: 60px;line-height: 60px;
          &:last-child{
            border-bottom: 1px solid #e4e4e4;
          }
          dt{
            font-weight: bold;
            width: 200px;
            text-align: center;
            background-color: rgb(51,51,51);
            color: rgb(255,255,255);
          }
          dd{
            color: #333;
            width: 140px;
            text-align: center;
            border-right: 1px solid #e4e4e4;

          }
        }
        .main{
          dd{
            background-color: #ccc;
            font-weight: bold;
          }
        }
      }



    }

    .content02{
      dl{
        display: flex;
        font-size: 13px;
        border-top: 1px solid #e4e4e4;
        &:last-child{
          border-bottom: 1px solid #e4e4e4;
        }
        dt{
          font-weight: bold;
          width: 25%;
          text-align: center;
          padding: 30px 10px 30px 40px;
          background: #f9f9f9;
          text-align: left;
        }
        dd{
          color: #333;
          padding: 30px 10px 30px 45px;
        }
      }
    }

    .content03{
      dl{
        display: flex;
        align-items: center;
        font-size: 13px;
        border-top: 1px solid #e4e4e4;
        background: #f9f9f9;
        &:last-child{
          border-bottom: 1px solid #e4e4e4;
        }
        dt{
          font-weight: bold;
          width: 25%;
          text-align: center;
          padding: 30px 10px 30px 40px;
          background: #f9f9f9;
          text-align: left;
        }
        dd{
          width: 75%;
          color: #333;
          padding: 30px 10px 30px 45px;
          background: #fff;
          line-height: 25px;
          ul{
            li{
              list-style: disc;
            }
          }
        }
      }
    }

    .content04{
      dl{
        display: flex;
        align-items: center;
        font-size: 13px;
        border-top: 1px solid #e4e4e4;
        background: #f9f9f9;
        &:last-child{
          border-bottom: 1px solid #e4e4e4;
        }
        dt{
          font-weight: bold;
          width: 25%;
          text-align: center;
          padding: 40px 10px 40px 40px;
          background: #f9f9f9;
          text-align: left;
        }
        dd{
          width: 75%;
          color: #333;
          padding: 40px 10px 40px 45px;
          background: #fff;
          line-height: 25px;
          .box{
            padding: 10px 20px 10px;
            background-color: rgb(238,238,238);
            border-radius: 5px;
            font-weight: 400;
            margin-bottom: 10px;
          }
        }
      }
    }
    

  }
}





/* ------------------review ------------------ */
.review_tab{
  width: 1420px;
  margin: 100px auto;
  .tab_menu{
    display: flex;
    text-align: center;
    box-sizing: border-box;
    .tab{
      border: none;
      background-color: #fff;
      width: 50%;
      height: 50px;
      line-height: 50px;
      border: 1px solid #ccc;
      border-right: none;
      font-weight: bold;
      font-size: 15px;
      border-bottom: 2.5px solid $pointcolor;
      box-sizing: border-box;
      &:last-child{
        border-right: 1px solid #ccc;
      }
    }
    .active02{
      border-right: 2.5px solid $pointcolor;
      border: 2px solid $pointcolor;
      border-bottom: none;
      box-sizing: border-box;
      &:last-child{
        border-right:  2.5px solid $pointcolor
      }
    }
  }

  .tab_content{

    .content{
      display: none;
      width: 1200px;
      height: 500px;
      margin: 0 auto;
      padding-top: 100px;
    }
    .active02{
      display: block;
    }





    .content01{



      .box{
        border: 1px solid #999;
        background: #f9f9f9;
        padding: 35px 25px;
        text-align: center;
        margin: 23px 0 32px;
        letter-spacing: -0.5px;
        p{
          line-height: 28px;
          span{
            color: #cf0a2c;
          }
        }
      }

      .review_box{
        display: flex;
        border: 1px solid #000;
        margin-top: 20px;
        background-color:pink;
        .review_img{
          width: 200px;
          img{
            width: 100%;
            height: auto;
            display: block;
          }
        }
        .review_stars{
