    }






    .content02{
      text-align: center;
      h4{
        font-size: 30px;
      }
      ul{
        width: 600px;
        text-align-last: left;
        margin: 0 auto;
        margin-top: 30px;
        li{
          margin-bottom: 5px;
          list-style:disc;
          font-size: 15px;
          color:#2b2a2a
        }
      }
      .btn_box{
        margin: 30px 0;
        button{
          border: none;
          background: #eaeaea;
          padding: 14px 16px 13px;
          font-size: 14px;
          width: 140px;
          margin-right: 20px;
          transition: all 0.3s;
          &:hover{
            background-color: #ccc;
          }
        }
      }

    }



    

  }
}


